@media (min-width: @mobile-width) {
  .slider__toggle {
    border: none;
    background-color: transparent;
    background-image: url("../images/slider-mobile.svg");
    background-repeat: no-repeat;
    width: 40px;
    height: 280px;
    position: absolute;
    top: -15px;
    left: 155px;
    z-index: 2;
  }
}

@media (min-width: @tablet-width) {
  .slider__toggle {
    background-color: transparent;
    background-image: url("../images/slider-tablet.svg");
    background-repeat: no-repeat;
    width: 40px;
    height: 560px;
    position: absolute;
    top: 10px;
    left: 370px;
    z-index: 2;
  }
}
